@import url(https://fonts.googleapis.com/css2?family=Sora&display=swap);
body{margin:0;padding:0;box-sizing:border-box;background-color:#12122d}

.container{max-width:1470px;display:block;margin:0 auto;margin-top:50px;padding-bottom:66px}

.card-container{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100%}.user-info{flex-direction:column;margin:0 auto;color:#eee;text-align:center;font-size:18px;font-family:Sora;font-style:normal;font-weight:normal}.user-button{display:block;margin:0 auto;width:248px;background-color:#52519D;color:#eee;border-radius:40px;line-height:70px;text-align:center;margin-top:30px;cursor:pointer;font-family:Sora;font-style:normal;font-weight:normal;font-weight:600;font-size:24px}

.posts-container{background-color:#333348;min-height:770px;position:relative;border-radius:40px;overflow:auto;width:100%}details>summary::marker{content:""}.posts-wrapper{position:absolute;margin:0 auto;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:60%}.post details .post__title{font-family:Sora;font-style:normal;font-weight:normal;font-weight:600;font-size:36px;color:#fff;cursor:pointer}.post details .post__descr{font-family:Sora;font-style:normal;font-weight:normal;color:#fff;margin-top:20px;font-size:18px}.post hr{margin-top:30px}.post:last-child hr{display:none}

.my-select{width:100%;margin-top:10px;background-color:#333348;color:#eee;height:40px}

.cards-wrapper{padding-top:35px;display:flex;justify-content:center;min-height:820px}.cards-wrapper__card{position:relative;height:320px;background-color:#333348;border-radius:40px;width:45%}.cards-wrapper-container{display:flex;flex-wrap:wrap;grid-gap:30px;gap:30px;justify-content:center;align-items:center;align-content:center;width:70%}.user-not-found{font-family:Sora;font-style:normal;font-weight:normal;color:#eee;text-align:center;font-size:24px}

.lds-heart{display:block;position:relative;width:80px;height:80px;-webkit-transform:rotate(45deg);transform:rotate(45deg);-webkit-transform-origin:40px 40px;transform-origin:40px 40px;margin:0 auto}.lds-heart div{top:32px;left:32px;position:absolute;width:32px;height:32px;background:#fff;-webkit-animation:lds-heart 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);animation:lds-heart 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);margin:0 auto}.lds-heart div:after,.lds-heart div:before{content:" ";position:absolute;display:block;width:32px;height:32px;background:#fff}.lds-heart div:before{left:-24px;border-radius:50% 0 0 50%}.lds-heart div:after{top:-24px;border-radius:50% 50% 0 0}@-webkit-keyframes lds-heart{0%{-webkit-transform:scale(0.95);transform:scale(0.95)}5%{-webkit-transform:scale(1.1);transform:scale(1.1)}39%{-webkit-transform:scale(0.85);transform:scale(0.85)}45%{-webkit-transform:scale(1);transform:scale(1)}60%{-webkit-transform:scale(0.95);transform:scale(0.95)}100%{-webkit-transform:scale(0.9);transform:scale(0.9)}}@keyframes lds-heart{0%{-webkit-transform:scale(0.95);transform:scale(0.95)}5%{-webkit-transform:scale(1.1);transform:scale(1.1)}39%{-webkit-transform:scale(0.85);transform:scale(0.85)}45%{-webkit-transform:scale(1);transform:scale(1)}60%{-webkit-transform:scale(0.95);transform:scale(0.95)}100%{-webkit-transform:scale(0.9);transform:scale(0.9)}}

.page-togglers{padding-top:56px;display:flex;justify-content:space-between}.previous-page,.next-page{font-family:Sora;font-style:normal;font-weight:normal;color:#eee;font-size:24px;cursor:pointer}.previous-page::before{display:inline-block;content:url(/test_task/static/media/left.39686770.svg);-webkit-transform:translateY(10px);transform:translateY(10px);margin-right:30px}.next-page::after{display:inline-block;content:url(/test_task/static/media/right.cac6de5b.svg);margin-left:30px;-webkit-transform:translateY(10px);transform:translateY(10px)}

input{padding:20px 0px 20px 35px;border-radius:40px;border:none;width:210px;background-color:#333348;color:#eee;font-size:24px;font-family:Sora;font-style:normal;font-weight:normal}input::-webkit-input-placeholder{color:#eee}input:-ms-input-placeholder{color:#eee}input::placeholder{color:#eee}

.navbar{display:flex;justify-content:space-between}.navbar__logo{font-family:Sora;font-style:normal;font-weight:normal;font-size:24px;line-height:70px;color:#EEEEEE}

